import React from 'react';
import { conUsuario } from '../Modelos/Usuario';
interface State {
    id_usuario: number
    nivel: number;
    usuario: string
    open: boolean
}
interface Props {
    update: () => void
}
export default class Header extends React.Component<Props, State> {
    state = { id_usuario: 0, nivel: 0, usuario: '', open: false };
    render() {
        let { usuario } = this.state;
        return (<nav className="main-header navbar navbar-expand navbar-success navbar-light">
            <ul className="navbar-nav">
                <li className="nav-item">
                    <div className="nav-link" onClick={this.open.bind(this)}><i className="fa fa-bars"></i></div>
                </li>
            </ul>
            <ul className="navbar-nav ml-auto">
                <li className="nav-item dropdown">
                    <div className="nav-link" data-toggle="dropdown" >
                        <i className="fa fa-comments"></i>
                        <span className="badge badge-danger navbar-badge">3</span>
                    </div>
                    <div className="dropdown-menu dropdown-menu-lg dropdown-menu-right">
                        <div className="dropdown-item">
                            <div className="media">
                                <img src="dist/img/user1-128x128.jpg" alt="User Avatar" className="img-size-50 mr-3 img-circle" />
                                <div className="media-body">
                                    <h3 className="dropdown-item-title">
                                        {usuario}
                                        <span className="float-right text-sm text-danger"><i className="fas fa-star"></i></span>
                                    </h3>
                                    <p className="text-sm">Call me whenever you can...</p>
                                    <p className="text-sm text-muted"><i className="far fa-clock mr-1"></i> 4 Hours Ago</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </li>
                <li className="nav-item">
                    <div className="nav-link" data-widget="control-sidebar" data-slide="true" role="button">
                        <i className="fa fa-th-large"></i>
                    </div>
                </li>
            </ul>
        </nav>)
    }

    open() {
        let body = document.getElementsByTagName('body')[0];
        let data = localStorage.getItem('menu');
        if (data === 'open') {
            localStorage.setItem('menu', 'cerrado')
            data = 'cerrado'
        } else {
            localStorage.setItem('menu', 'open')
            data = 'open'
        }
        if (body) {
            if (data === 'cerrado')
                body.classList.add('sidebar-collapse')
            else
                body.classList.remove('sidebar-collapse')
        }
        this.props.update();
    }

    componentDidMount() {
        let body = document.getElementsByTagName('body')[0];
        let data = localStorage.getItem('menu');
        if (!data) {
            data = "open";
            localStorage.setItem('menu', 'open')
        }
        if (body) {
            if (data === 'cerrado')
                body.classList.add('sidebar-collapse')
            else
                body.classList.remove('sidebar-collapse')
        }
        conUsuario().get('/api/Usuario/actual', this.setState.bind(this));
    }
}