import React from "react"
import Gauge from "../../Utiles/Charts/Gauge";
import NuevoEquilibrio from './NuevoEquilibrio'
export default class Equilibrio extends React.Component<{}, { equilibrio: number, ventas: number, open: boolean }>{
    state = { equilibrio: 100, ventas: 0, open: false };
    render() {
        const { equilibrio, ventas, open } = this.state;
        return <>
            <NuevoEquilibrio open={open} close={() => { this.setState({ open: false }); this.send() }} />
            <Gauge
                label='Bs'
                value={ventas}
                red={0}
                yellow={equilibrio / 2}
                green={equilibrio}
            />
        </>
    }
    async send() {
        try {
            let token = localStorage.getItem('Authorization');
            let response = await fetch('/ajax/widgets/equilibrio.php', { headers: { 'Authorization': 'Bearer ' + token } });
            let datos = await response.json();
            this.setState({ equilibrio: parseFloat(datos[0]), ventas: Math.floor(datos[1]) });
            if (parseFloat(datos[0]) === 0) this.setState({ equilibrio: 100, open: true });
        } catch (error) {
            console.log(error);
            this.setState({ equilibrio: 100, ventas: 0 });
        }
    }
    componentDidMount() { this.send() }
}