import React from 'react';
import { Button, Col, InputGroup, Row } from 'react-bootstrap';
import { Detalles } from '../../../Modelos/General';
import { conProducto, Producto } from '../../../Modelos/Productos';
import FilaCompra from './FilaCompra';
import FilaSeleccion from './FilaSeleccion';
import FilaFactura from './FilaFactura';
import FilaEntrada from './FilaEntrada';
import FilaPrecio from './FilaPrecio';
import FilaSalida from './FilaSalida';
import FilaUtilidad from './FilaUtilidad';
import FilaCotizacion from './FilaCotizacion';
import FilaDolar from './FilaDolar';
import DepartamentoSelector from '../../Departamento/Selector';
import ModalBusqueda from '../../../Utiles/Buscadores/Modal';
import { InputText } from '../../../Utiles/Inputs';
interface Props {
    type: 'Cotizacion' | 'Compra' | 'Factura' | 'Nota' | 'Orden' | 'SALIDA' | 'ENTRADA' | 'COSTO' | 'UTILIDAD' | 'Seleccion';
    add: (p: Detalles) => void
    dolar?: boolean
    tasa?: number
}
export default class BuscarProducto extends ModalBusqueda<Props> {
    con = conProducto();
    buscarEn = ['codigo', 'descripcion'];
    departamento: string = 'TODO';
    Title() { return <h4 className='mr-auto'>BUSCAR PRODUCTOS</h4> }
    setDepartamento(departamento: string) {
        this.departamento = departamento;
        this.forceUpdate();
    }
    Buscador() {
        return <Row style={{ marginBottom: '15px' }}>
            <Col md={10}>
                <InputGroup>
                    <InputGroup.Prepend>
                        <InputGroup.Text>
                            {this.buscarLabel}
                        </InputGroup.Text>
                    </InputGroup.Prepend>
                    <InputText className='form-control' upper setData={this.setState} update='buscar' value={this.state.buscar} />
                    <DepartamentoSelector width='300px' defaultValue={this.departamento} change={this.setDepartamento.bind(this)} />
                    <InputGroup.Append>
                        <Button variant="primary">
                            <span className="fa fa-search"></span> BUSCAR
                        </Button>
                    </InputGroup.Append>
                </InputGroup>
            </Col>
            <Col md={2}>
                <Button className='float-right' onClick={this.set.bind(this)} variant="info">
                    <span className="fa fa-refresh"></span> ACTUALIZAR
                </Button>
            </Col>
        </Row>
    }
    Header() {
        const { Th } = this;
        return <tr className="info">
            <Th label='codigo' width='100px'>CODIGO</Th>
            <Th label='descripcion' width='auto'>DESCRIPCION</Th>
            {this.head()}
            <Th width='40px'> </Th>
        </tr>
    }
    Row(c: Producto) {
        const { type, tasa, dolar, add } = this.props;
        switch (type) {
            case 'Seleccion':
                return <FilaSeleccion key={c.codigo + 'Seleccion'} producto={c} add={add} />
            case 'Compra': case 'Orden':
                return <FilaCompra key={c.codigo + 'Compra'} producto={c} add={add} />
            case 'Nota': case 'Factura':
                return <FilaFactura key={c.codigo + 'Factura'} producto={c} add={add} />
            case 'ENTRADA':
                return <FilaEntrada key={c.codigo + 'ENTRADA'} producto={c} add={add} />;
            case 'SALIDA':
                return <FilaSalida key={c.codigo + 'SALIDA'} producto={c} add={add} />;
            case 'COSTO':
                return <FilaPrecio key={c.codigo + 'COSTO'} producto={c} add={add} />;
            case 'UTILIDAD':
                return <FilaUtilidad key={c.codigo + 'UTILIDAD'} producto={c} add={add} />;
            case 'Cotizacion':
                return (dolar) ?
                    <FilaDolar tasa={tasa ? tasa : 1} key={c.codigo + 'dolar'} producto={c} add={add} /> :
                    <FilaCotizacion key={c.codigo + 'Cotizacion'} producto={c} add={add} />;
        }
    }
    filter() {
        const { departamento, props: { type } } = this;
        let itens = this.ordenar();
        return itens.filter((c: Producto) => {
            if (c.estatus === 0) return false;
            if (departamento !== 'TODO' && c.departamento !== departamento) return false;
            if ((type === 'Factura' || type === 'Nota') && c.cantidad < .01) return false;
            if ((type === 'ENTRADA' || type === 'SALIDA') && c.inventario === 1) return false;
            return true;
        });
    }
    componentDidMount() {
        this.set();
    }
    head() {
        const { Th } = this;
        switch (this.props.type) {
            case 'ENTRADA':
                return <>
                    <Th width='100px'>CANTIDAD</Th>
                </>;
            case 'SALIDA':
                return <>
                    <Th width='100px'>CANTIDAD</Th>
                    <Th width='150px'>DISPONIBLE</Th>
                </>;
            case 'COSTO':
                return <>
                    <Th width='300px'>COSTO</Th>
                </>;
            case 'UTILIDAD':
                return <>
                    <Th width='150px'>PRECIO 1</Th>
                    <Th width='150px'>PRECIO 2</Th>
                    <Th width='150px'>PRECIO 3</Th>
                </>;
            case 'Seleccion':
                return <></>;
            case 'Factura':
                return <>
                    <Th width='100px'>CANTIDAD</Th>
                    <Th width='100px'>DISPONIBLE</Th>
                    <Th width='150px'>PRE/UNT</Th>
                    <Th width='200px'>PRECIO</Th>
                </>
        }
        return <>
            <Th width='100px'>CANTIDAD</Th>
            <Th width='150px'>PRE/UNT</Th>
            <Th width='200px'>PRECIO</Th>
        </>
    }
}