import React from "react";
import { Accordion, Button, Card } from "react-bootstrap";
import { toast } from "react-toastify";
import { conNota, Nota, sentNota } from "../../Modelos/Factura";
import { GeneralFila, generalNull } from "../../Modelos/General";
import { InputArea } from "../../Utiles/Inputs";
import ClienteSelector from "../Clientes/ClienteSelector";
import BuscarCotizacion from "../Cotizacion/BuscarCotizacion";
import AuxiliarFactura from "../Facturas/Nuevo/AuxiliarFactura";
import BuscarNota from "./BuscarNota";
interface State extends Nota {
    modalCotizacion: boolean
    modalNota: boolean
}
export default class NuevaNota extends React.Component<any, State>{
    con = conNota();
    impuesto = 0;
    constructor(p: any) {
        super(p);
        this.setState = this.setState.bind(this)
        this.setCliente = this.setCliente.bind(this)
        this.state = { ...generalNull, cod_cliente: '', modalCotizacion: false, modalNota: false };
    }
    render() {
        let { state: { modalCotizacion, modalNota, nota, cod_cliente, codigo }, close, setCliente, cargarCotizacion, cargarNota, procesar, setState } = this;
        if (codigo > 0)
            return <iframe src={"/api/Nota/PDF/" + codigo} width='100%' height='900wh' title="print" scrolling='no' frameBorder='0' />

        return <Card>
            <Card.Header className='bg-info'>
                <h4 className='mr-auto'><i className="fa fa-edit"></i> NUEVA NOTA DE ENTREGA</h4>
            </Card.Header>
            <Card.Body>
                <ClienteSelector type='Cliente' Cliente={cod_cliente} add={setCliente} />
                <Accordion>
                    <Accordion.Toggle eventKey='0' as={Button} variant='primary'>NOTA</Accordion.Toggle>
                    <Accordion.Collapse eventKey='0'><Card>
                        <Card.Body>
                            <InputArea rows={4} maxLength={600} value={nota} setData={this.setState} update="nota" />
                        </Card.Body>
                    </Card>
                    </Accordion.Collapse>
                </Accordion>
                <BuscarCotizacion open={modalCotizacion} add={cargarCotizacion} close={close} />
                <BuscarNota open={modalNota} add={cargarNota} close={close} />
                <AuxiliarFactura key="auxNota" Prosesar={procesar} setData={setState} data={this.state} />
            </Card.Body>
        </Card>
    }
    close() {
        this.setState({
            modalCotizacion: false,
            modalNota: false
        })
    }
    cargarCotizacion(d: GeneralFila) {
        this.con.get('api/Cotizacion/detalles/' + d.codigo, data => {
            data.id_cotizacion = data.codigo;
            data.codigo = 0;
            this.setState(data)
        })
    }
    cargarNota(d: GeneralFila) {
        this.con.detalle(d.codigo, (data) => {
            data.codigo = 0;
            data.nota = this.state.nota;
            this.setState(data)
        });
    }
    componentDidMount() {
        this.con.get('api/Region/get', data => {
            this.impuesto = data.impuesto;
        })
    }
    setCliente(p: string) {
        this.setState({ cod_cliente: p });
    }
    procesar() {
        let procesar = true;
        if (this.state.cod_cliente === "") {
            toast('DEBE SELECCIONAR UN CLIENTE', { type: toast.TYPE.ERROR });
            procesar = false;
        } else if (this.state.detalles.length === 0) {
            toast('DEBE SELECCIONAR AL MENOS UN PRODUCTO', { type: toast.TYPE.ERROR });
            procesar = false;
        } else if (procesar)
            this.state.detalles.forEach(p => {
                if (p.unidades > p.cantidad) {
                    if (p.tipo !== 2) {
                        toast('NO HAY SUFICENTE ' + p.descripcion + ' PARA COMPLETAR LA OPERACION', { type: toast.TYPE.ERROR });
                        procesar = false;
                    }
                } else if (p.costo > p.precio) {
                    toast(p.descripcion + ' ESTA POR DEBAJO DE SU COSTO REGISTRADO', { type: toast.TYPE.ERROR });
                    procesar = false;
                }
            })
        if (procesar)
            this.con.nuevo(sentNota(this.state, this.impuesto),
                data => { this.setState({ codigo: data }) });
    }
}