import React from 'react'
import { FormControl } from 'react-bootstrap';
import { conDepartamento, Departamento } from '../../Modelos/Productos';
interface Props {
    change: (r: string) => void
    defaultValue: string
    width?: string
}
interface State {
    Departamentos: Departamento[] | 'carro'
}
export default class DepartamentoSelector extends React.Component<Props, State>{
    constructor(p: Props) {
        super(p);
        this.state = { Departamentos: 'carro' };
    }
    setUsers(c: Departamento[]) {
        this.setState({ Departamentos: c })
    }
    render() {
        return <FormControl as='select' style={{ width: this.props.width, maxWidth: this.props.width }} onChange={this.setRol.bind(this)} value={this.props.defaultValue}>
            <option value='TODO'>SELECCIONAR UN DEPARTAMENTO</option>
            {(this.state.Departamentos !== 'carro') ? this.state.Departamentos.map(c => {
                return <option key={"depas" + c.codigo} value={c.codigo}>{c.descripcion}</option>
            }) : <></>}
        </FormControl>
    }
    componentDidMount() {
        this.set();
    }
    setRol(Rol: React.ChangeEvent<HTMLSelectElement>) {
        let value = Rol.currentTarget.value as string
        this.props.change(value)
    }
    set() {
        conDepartamento().lista(this.setUsers.bind(this));
    }
}