import React from 'react';
import {  Col, Row } from 'react-bootstrap';
import { Route, Link } from "react-router-dom"
import Ventas from './Ventas/reportes';
import Compras from './Compras/Reportes';
import Vendedores from './Ventas/Vendedores';
import Inventario from './Inventario';
import Historico from './Historico';
import Cotizaciones from './Cotizacion/reporte';
import Notas from './Notas/reporte';
import Ordenes from './Orden/reporte';
import HistoricoCompra from './Compras/Historico';
import HistoricoFactura from './Ventas/Historico';
import HistoricoCotizacion from './Cotizacion/Historico';
import HistoricoNota from './Notas/Historico';
import HistoricoOrden from './Orden/Historico';
import ItensCompra from './Compras/Itens';
import ItensCotizacion from './Cotizacion/Itens';
import ItensFactura from './Ventas/Itens';
import ItensNota from './Notas/Itens';
import ItensOrden from './Orden/Itens';
import { Menus } from '../../Utiles/Componentes/Menus';
export default class Reportes extends React.Component {
    render() {
        return (<Row style={{ minHeight: "calc(100vh - 145px)" }}>
            <Col md={3} style={{ minHeight: "calc(100vh - 145px)" }}>
                <Menus title='REPORTES DE VENTAS' >
                    <Link className='btn btn-default' to='/reporte_ventas'>
                        VENTAS
                    </Link>
                    <Link className='btn btn-default' to='/reporte_vendedor'>
                        VENDEDORES
                    </Link>
                    <Link className='btn btn-default' to='/historico_ventas'>
                        VENTAS DE UN PORDUCTO
                    </Link>
                    <Link className='btn btn-default' to='/productos_ventas'>
                        PRODUCTOS POR VENTAS
                    </Link>
                </Menus>
                <Menus title='REPORTES DE NOTAS DE ENTREGA' >
                    <Link className='btn btn-default' to='/reporte_notas'>
                        NOTAS DE ENTREGA
                    </Link>
                    <Link className='btn btn-default' to='/historico_notas'>
                        N.E DE UN PORDUCTO
                    </Link>
                    <Link className='btn btn-default' to='/productos_notas'>
                        PRODUCTOS POR N.E
                    </Link>
                </Menus>
                <Menus title='REPORTES DE COTIZACIONES' >
                    <Link className='btn btn-default' to='/reporte_cotizacion'>
                        COTIZACIONES
                    </Link>
                    <Link className='btn btn-default' to='/historico_cotizacion'>
                        COTIZACIONES DE UN PORDUCTO
                    </Link>
                    <Link className='btn btn-default' to='/productos_cotizacion'>
                        PRODUCTOS POR COTIZACIONES
                    </Link>
                </Menus>
                <Menus title='REPORTES DE COMPRAS' >
                    <Link className='btn btn-default' to='/reporte_compras'>
                        COMPRAS
                    </Link>
                    <Link className='btn btn-default' to='/historico_compras'>
                        COMPRAS DE UN PORDUCTO
                    </Link>
                    <Link className='btn btn-default' to='/productos_compras'>
                        PRODUCTOS POR COMPRAS
                    </Link>
                </Menus>
                <Menus title='REPORTES DE ORDENES DE COMPRA' >
                    <Link className='btn btn-default' to='/reporte_ordenes'>
                        ORDENES DE COMPRA
                    </Link>
                    <Link className='btn btn-default' to='/historico_ordenes'>
                        O.C DE UN PORDUCTO
                    </Link>
                    <Link className='btn btn-default' to='/productos_ordenes'>
                        PRODUCTOS POR O.C
                    </Link>
                </Menus>
                <Menus title='REPORTES DE INVENTARIO' >
                    <Link className='btn btn-default' to='/reporte_inventario'>
                        INVENTARIO
                    </Link>
                    <Link className='btn btn-default' to='/historico'>
                        HISTORICO
                    </Link>
                </Menus>
            </Col>
            <Col md={9} style={{ minHeight: "calc(100vh - 145px)" }}>
                <Route path="/reporte_compras" component={Compras} />
                <Route path="/reporte_ventas" component={Ventas} />
                <Route path="/reporte_cotizacion" component={Cotizaciones} />
                <Route path="/reporte_notas" component={Notas} />
                <Route path="/reporte_ordenes" component={Ordenes} />
                <Route path="/productos_compras" component={ItensCompra} />
                <Route path="/productos_ventas" component={ItensFactura} />
                <Route path="/productos_cotizacion" component={ItensCotizacion} />
                <Route path="/productos_notas" component={ItensNota} />
                <Route path="/productos_ordenes" component={ItensOrden} />
                <Route path="/historico_compras" component={HistoricoCompra} />
                <Route path="/historico_ventas" component={HistoricoFactura} />
                <Route path="/historico_cotizacion" component={HistoricoCotizacion} />
                <Route path="/historico_notas" component={HistoricoNota} />
                <Route path="/historico_ordenes" component={HistoricoOrden} />
                <Route path="/reporte_vendedor" component={Vendedores} />
                <Route path="/rubros" component={Ventas} />
                <Route path="/reporte_inventario" component={Inventario} />
                <Route path="/seguimiento" component={Ventas} />
                <Route path="/historico" component={Historico} />
            </Col>
        </Row>)
    }
}