import React from 'react';
import { Link } from 'react-router-dom';

export default class Menu extends React.Component<{ state: string }> {
    render() {
        return <aside className="main-sidebar sidebar-dark-primary">
            <a href="/panel_ad" className="brand-link bg-success" style={{ width: '100%', height: '57px' }}>
                <img width={33} height={33} src="/logos/logo.svg" alt="logo" className="brand-image img-circle" style={{ opacity: .8 }} />
                <img src="/logos/texto.svg" alt="letras" className="brand-image brand-text" style={{ opacity: .8, transitionDelay: '.2s' }} />
            </a>
            <div className="sidebar">
                <nav className="mt-2">
                    <ul className="nav nav-pills nav-sidebar flex-column" data-widget="treeview" role="menu" data-accordion="false">
                        <li className="nav-item">
                            <a href="/panel_ad" className="nav-link">
                                <i className="nav-icon fa fa-tachometer"></i> <p>INICIO</p>
                            </a>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="/ver_ordenes">
                                <i className="nav-icon fa fa-paperclip"></i> <p style={{ width: "200px" }}> ORDENES DE COMPRA </p>
                            </Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="/ver_compras">
                                <i className="nav-icon fa fa-shopping-cart"></i> <p> COMPRAS </p>
                            </Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="/ver_cotizaciones">
                                <i className="nav-icon fa fa-calculator"></i> <p> COTIZACIONES </p>
                            </Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="/ver_notas">
                                <i className="nav-icon fa fa-paper-plane"></i> <p> NOTAS DE ENTREGA </p>
                            </Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="/ver_facturas">
                                <i className="nav-icon fa fa-usd"></i> <p> FACTURAS </p>
                            </Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="/ver_proveedores">
                                <i className="nav-icon fa fa-truck"></i> <p> PROVEEDORES</p>
                            </Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="/ver_clientes">
                                <i className="nav-icon fa fa-user"></i><p> CLIENTES</p>
                            </Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="/reporte_compras">
                                <i className="nav-icon fa fa-bar-chart"></i><p> REPORTES</p>
                            </Link>
                        </li>
                        <li className='nav-item'>
                            <Link className="nav-link" to="ver_productos">
                                <i className="nav-icon fa fa-laptop"></i> <p> INVENTARIO</p>
                            </Link>
                        </li>
                        <li className='nav-item'>
                            <Link className="nav-link" to="ajuste_inventario">
                                <i className="nav-icon fa fa-th-large"></i> <p>AJUSTES</p>
                            </Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="ver_usuarios">
                                <i className="nav-icon fa fa-users"></i><p> USUARIOS</p>
                            </Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="/conf_ventas">
                                <i className="nav-icon fa fa-wrench"></i><p> CONFIGURACION</p>
                            </Link>
                        </li>
                    </ul>
                </nav>
            </div>
        </aside>
    }
}