import React from "react";
import { Modal } from "react-bootstrap";
import { Conexion } from "../../Config/Coneccion";
import { conCotizacion } from "../../Modelos/Factura";
import Tabla from "../Tabla";
interface Props {
    title?: string
    open: boolean,
    close: () => void
    add: (d: any) => void
}
export default class ModalBusqueda<p = {}> extends Tabla<Props & p>{
    con: Conexion = conCotizacion();
    constructor(p: any) {
        super(p);
        this.Title = this.Title.bind(this);
    }
    Title() {
        return <></>;
    }
    render() {
        let { Body, Buscador, Title, props: { open, close } } = this;
        return <Modal show={open} onHide={close} size='xl'>
            <Modal.Header closeButton>
                <Title />
            </Modal.Header>
            <Modal.Body>
                <div style={{ padding: "10px 10px 10px 10px" }}>
                    <Buscador />
                    <Body />
                </div>
            </Modal.Body>
        </Modal>
    }
    set() {
        this.con.cargarListaWeb((itens) => { this.setState({ itens }) });
    }
    componentDidMount() {
        this.set();
    }
}