import React from 'react';
import { BrowserRouter as Router, Switch, Route } from "react-router-dom"
import 'react-toastify/dist/ReactToastify.css';
import { ToastContainer } from 'react-toastify';
import Header from './Template/Header';
import Menu from './Template/Menu';
import Footer from './Template/Footer';
import ListaClientes from './App/Clientes';
import ListaCompra from './App/Compra';
import NuevaCompra from './App/Compra/Nuevo';
import ListaCotizaciones from './App/Cotizacion';
import NuevaCotizacion from './App/Cotizacion/Nuevo';
import ListaFactura from './App/Facturas';
import NuevaFactura from './App/Facturas/Nuevo';
import ListaNotas from './App/Nota';
import NuevaNota from './App/Nota/Nuevo';
import ListaProveedores from './App/Proveedores';
import ListaOrden from './App/Orden';
import NuevaOrden from './App/Orden/Nuevo';
import ListaProductos from './App/Productos';
import ListaUsuarios from './App/Usuarios';
import Config from './App/Config';
import Reportes from './App/Reportes';
import Panel from './App/Panel';
import ListAjustes from './App/Ajustes';
import NuevoAjuste from './App/Ajustes/Nuevo';

export default class App extends React.Component {
  render() {
    let data = localStorage.getItem('menu');
    if (!data) {
      data = "open";
      localStorage.setItem('menu', 'open');
    }
    let styles: any = { minHeight: "calc(100vh - 150px)", marginRight: '15px', transition: 'width 2s' };
    if (data === 'cerrado') styles.marginLeft = "90px";
    else styles.marginLeft = "270px";
    return (<Router>
      <Header update={this.forceUpdate.bind(this)} key='headerBar' />
      <Menu state={data} key='MenuAside' />
      <section style={styles} className="content">
        <ToastContainer position='bottom-right' />
        <br />
        <Switch>
          <Route path="/panel_ad" component={Panel} />
          <Route path="/ver_cotizaciones" component={ListaCotizaciones} />
          <Route path="/nueva_cotizacion" component={NuevaCotizacion} />
          <Route path="/ver_ordenes" component={ListaOrden} />
          <Route path="/nueva_orden" component={NuevaOrden} />
          <Route path="/ver_facturas" component={ListaFactura} />
          <Route path="/nueva_factura" component={NuevaFactura} />
          <Route path="/ver_notas" component={ListaNotas} />
          <Route path="/nueva_nota" component={NuevaNota} />
          <Route path="/ver_compras" component={ListaCompra} />
          <Route path="/nueva_compra" component={NuevaCompra} />
          <Route path="/ver_proveedores" component={ListaProveedores} />
          <Route path="/ver_clientes" component={ListaClientes} />
          <Route path="/ver_productos" component={ListaProductos} />
          <Route path="/ver_usuarios" component={ListaUsuarios} />
          <Route path={[
            "/conf_ventas",
            "/conf_region",
            "/conf_orden",
            "/conf_compra",
            "/conf_cotizacion",
            "/conf_nota",
            "/conf_factura",
            '/conf_ajustes',
            '/conf_reportes',
            "/conf_empresa",
            "/departamentos",
            "/tipo_producto",
            "/unidades",
            "/moneda",
            "/utilidades",
            "/costos",
            "/roles"
          ]} component={Config} />
          <Route path={[
            "/reporte_compras",
            "/reporte_ventas",
            '/reporte_cotizacion',
            '/reporte_notas',
            '/reporte_ordenes',
            "/productos_compras",
            "/productos_ventas",
            '/productos_cotizacion',
            '/productos_notas',
            '/productos_ordenes',
            "/historico_compras",
            "/historico_ventas",
            '/historico_cotizacion',
            '/historico_notas',
            '/historico_ordenes',
            "/rubros",
            "/reporte_vendedor",
            "/reporte_inventario",
            "/seguimiento",
            "/historico",
          ]} component={Reportes} />
          <Route path="/ajuste_inventario" component={ListAjustes} />
          <Route path="/nuevo_ajuste" component={NuevoAjuste} />
        </Switch>
      </section>
      <Footer />
    </Router >);
  }
}