import React from "react"
import Gauge from "../../Utiles/Charts/Gauge";
export default class Utilidad extends React.Component<{}, { utilidad: number }>{
    state = { utilidad: 0 };
    render() {
        const { utilidad } = this.state;
        return <Gauge
            label='%'
            value={utilidad}
            red={0}
            yellow={10}
            green={30}
        />
    }
    componentDidMount() {
        this.send()
    }
    async send() {
        try {
            let token = localStorage.getItem('Authorization');
            let response = await fetch('/ajax/widgets/utilidad.php', { headers: { 'Authorization': 'Bearer ' + token } });
            let datos = await response.json();
            this.setState({ utilidad: parseFloat(datos[0]) });
        } catch (error) {
            console.log(error);
            this.setState({ utilidad: 0 });
        }
    }
}