
import React from 'react';
import { conNota } from '../../Modelos/Factura';
import FilaProto from '../../Utiles/Prototipos/FilaProto';

export default class FilaNota extends FilaProto {
    con=conNota();
    constructor(p: any) {
        super(p);
        this.Label = 'NOTA DE ENTREGA';
        this.tipo = 'Nota';
    }
    getStatus() {
        switch (this.props.data.status) {
            case 0:
                return <span className="badge badge-danger">CANCELADA</span>
            case 1:
                return <span className="badge badge-warning">ENTREGADO</span>
            case 2:
                return <span className="badge badge-success">FACTURADA</span>
        }
    }
}