import React from 'react'
import { Button, FormControl } from 'react-bootstrap'
import { Detalles } from '../../../Modelos/General'
import { numberFormat } from '../../../Utiles/Formatos'
import { InputNumber } from '../../../Utiles/Inputs'

interface Props {
    producto: Detalles,
    nota?: number
    edit: (iten: Detalles) => void,
    remove: (iten: Detalles) => void
}
export default class FilaProducto extends React.Component<Props, Detalles>{
    constructor(p: Props) {
        super(p)
        this.state = { ...p.producto }
        this.setState = this.setState.bind(this)
    }
    render() {
        let { unidades, precio, descripcion, codigo, costo, precio1, precio2, precio3, cantidad, inventario } = this.state,
            p = (costo * (1 + precio1 / 100)).toFixed(2);
        return <tr className={(cantidad < unidades) ? 'danger' : (costo > precio) ? 'warning' : ''}>
            <td>{codigo}</td>
            <td>{descripcion}</td>
            <td><InputNumber min={.01} setData={this.setState} value={unidades} update="unidades" /></td>
            <td><FormControl style={{ textAlign: 'right' }} disabled value={(inventario === 1) ? 'NO APLICA' : numberFormat(cantidad)} /></td>
            <td>
                <InputNumber step='0.01' setData={this.setState} value={precio} update="precio"
                    list={costo + 'list'} min={parseFloat(p)} />
                <datalist id={costo + 'list'}>
                    <option>{p}</option>
                    <option>{(costo * (1 + precio2 / 100)).toFixed(2)}</option>
                    <option>{(costo * (1 + precio3 / 100)).toFixed(2)}</option>
                </datalist>
            </td>
            <td>
                <span style={{ width: '100%', position: 'relative', backgroundColor: "#eee", textAlign: 'right' }}
                    className="form-control">{numberFormat(precio * unidades)}</span>
            </td>
            <td style={{ textAlign: 'right' }}>
                <Button variant="danger" onClick={() => { this.props.remove(this.state) }} data-tip="QUITAR PRODUCTO">
                    <i className="fa fa-ban"></i>
                </Button>
            </td>
        </tr>
    }
    componentWillUpdate(p: Props, s: Detalles) {
        if (s.precio !== this.state.precio || s.unidades !== this.state.unidades)
            this.props.edit(s)
    }
}